<template>
  <div v-if="isLoggedIn">
    <div id="a">
      <div class="container">
        <div class="form-group">
          <h2></h2>
        </div>
        <table border="1px" align=center cellspacing="0">
          <tr>
            <th>ID</th>
            <th></th>
            <th></th>
            <th></th>
            <th>ID</th>
            <th></th>
            <th></th>
          </tr>
          <tr v-for="(order, index) in paginatedItems" :key="index">
            <td>{{ order.orderid }}</td>
            <td>{{ order.address }}</td>
            <td>{{ order.telephone }}</td>
            <td>{{ order.buyername }}</td>
            <td>{{ order.goodid }}</td>
            <td>
              <button v-if="order.orderstate < 4 && order.orderstate > 0" @click="confirmOrder(order.orderid)"
                class="green-btn"></button>
              <button v-if="order.orderstate <= 4 && order.orderstate > 0" @click="cancelOrder(order.orderid)"
                class="red-btn"></button>
              <span v-if="order.orderstate < 0 || order.orderstate > 4"></span>
            </td>
            <td>
              {{ getOrderStatus(order.orderstate) }}
            </td>
          </tr>
        </table>
        <div class="pagination">
          <button @click="goToPrevPage" :disabled="isPrevDisabled" class="prev"></button>
          <span>{{ currentPage }}/{{ totalPages }}</span>
          <button @click="goToNextPage" :disabled="isNextDisabled" class="next"></button>
        </div>
      </div>
    </div>
  </div>
  <div v-else class="else">
    <a href="login"></a>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      isLoggedIn: true, // 
      orders: [],
      currentPage: 1,
      itemsPerPage: 5,
      //  totalItems 
      totalItems: 6,
      currentUser: {},
    };
  },
  methods: {
    getOrderStatus(state) {
      // 
      // 
      const statusMap = {
        0: '',
        1: '',
        2: '',
        3: '',
        4: '',
        5: '', //  5 
      };
      return statusMap[state] || '';
    },
    handleOrderAction(orderId, action) {
      //  action 
      switch (action) {
        case 'wait':
          // 
          break;
        case 'confirm':
          this.confirmOrder(orderId);
          break;
        case 'prepare':
          // 
          break;
        case 'ship':
          // 
          break;
        case 'cancel':
          this.cancelOrder(orderId);
          break;
        // ...
      }
    },
    confirmOrder(orderId) {
      // 
      const order = this.orders.find(o => o.orderid === orderId);
      if (order) {
        //  1 
        let updatedOrderState = order.orderstate + 1;
        // 
        axios.post('/order/confirmorder-control', { orderid: orderId, orderstate: updatedOrderState })
          .then(response => {
            if (response.data && response.data.msg === '') {
              // 
              order.orderstate = updatedOrderState;
              alert("");
              // 
            } else {
              alert("");
            }
          })
          .catch(error => {
            console.error(':', error);
          });
      }
    },
    cancelOrder(orderId) {
    // 
    const order = this.orders.find(o => o.orderid === orderId);
    if (order) {
      // 
      axios.post('/order/deleteorder-control', { orderid: orderId, orderstate: -1 })
        .then(response => {
          if (response.data && response.data.msg === '') {
            // 
            order.orderstate = -1;
            alert("");
            // 
          } else {
            alert("");
          }
        })
        .catch(error => {
          console.error(':', error);
        });
    }
  },
    goToPrevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
        this.fetchOrders(); // 
      }
    },
    goToNextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
        this.fetchOrders(); // 
      }
    },
    fetchOrders() {
      // 
      axios.get('/order/showorderinfo-control')
        .then(response => {
          console.log(response.data.data); // 
          if (response.data && response.data.data) {
            this.orders = response.data.data; // 
            console.log("");
          } else {
            console.error("");
          }
        })
        .catch(error => {
          console.error('', error);
        });
    },

    async fetchUsrFromSession() {
      try {
        const response = await axios.get('/now-usr');
        this.currentUser = response.data;
        if (this.currentUser) {
          this.isLoggedIn = true;
        }
      } catch (error) {
        console.error(':', error);
        this.isLoggedIn = false;
      }
    }
  },
  computed: {
    paginatedUsers() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.filteredUsers.slice(start, end);
    },

    totalPages() {
      return Math.ceil(this.totalItems / this.itemsPerPage);
    },
    isPrevDisabled() {
      return this.currentPage <= 1;
    },
    isNextDisabled() {
      return this.currentPage >= this.totalPages;
    },
    filteredOrders() {
      //  -1 
      return this.orders.filter(order => order.orderstate !== -2);
    },
    paginatedItems() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = this.currentPage * this.itemsPerPage;
      return this.orders.slice(start, end);
    },
  },
  mounted() {
    this.fetchUsrFromSession().then(() => {
      if (this.isLoggedIn) {
        this.fetchOrders();
      }
    });
  },
  created() {
    this.fetchOrders(); // 
  },
}
</script>

